<template>
    <div class="DialogVisitor">
         <el-dialog 
            :title="dialog.title" 
            :visible.sync="dialog.show"
            :close-on-click-modal='false'
            :close-on-press-escape='false'
            :modal-append-to-body="false">
            <div class="form">
                <el-form 
				   :model="form"
                    :rules="form_rules"
                    label-width="120px" 
                    style="margin:10px;width:auto;">

                    <el-form-item prop='name' label=":">
                        <el-input type="name" v-model="form.name" clearable></el-input>
                    </el-form-item>

                    <el-form-item prop='phone'  label=":">
                        <el-input type="phone" v-model="form.phone" clearable></el-input>
                    </el-form-item>

                    <el-form-item prop='vsisitPerson' label=":">
                        <el-input type="vsisitPerson" v-model="form.vsisitPerson" clearable></el-input>
                    </el-form-item>

                    <el-form-item prop='visitTime' label=":">
                        <!-- <el-input type="cash" v-model="form.cash"></el-input> -->
						
						<el-date-picker v-model="form.visitTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="">
						</el-date-picker>
                    </el-form-item>

                    <el-form-item prop='receptionist' label=":">
                        <el-input type="receptionist" v-model="form.receptionist" clearable></el-input>
                    </el-form-item>

                    <el-form-item prop='note' label=":">
                        <el-input type="note" v-model="form.content" clearable></el-input>
                    </el-form-item>

                     <el-form-item label=""> 
                        <el-input type="textarea" v-model="form.note" clearable></el-input>
                    </el-form-item>

                    <el-form-item  class="text_right">
                        <el-button @click="dialog.show = false"> </el-button>
                        <el-button type="primary" @click='onSubmit()'>  </el-button>
                    </el-form-item>

                </el-form>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import qs from 'qs'
export default {
  name: "DialogVisitor",
  props: {
    dialog: Object,
    form: Object
  },
  data() {
    return {
	 
      form_rules: {
        name: [
          { required: true, message: "", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "", trigger: "blur" }
        ],
        vsisitPerson: [
          { required: true, message: "", trigger: "blur" }
        ],
        visitTime: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  
  methods: {
    onSubmit(){
      const data1 = this.form;
      console.log(data1)
      this.$axios({
        method: 'POST',
        url:'/visitor/saveVisitor',
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
        data: qs.stringify(data1),  }) 
        .then(res => {
        // 
        this.$message({
          message: "",
          type: "success"
        });
          this.dialog.show = false;
          this.$emit("update");
        });
    }
  }
};
</script>




