<template>
	<div class="infoshow">
		<div>
			<el-form :inline="true" ref="search_data" :model='search_data'>
				<el-form-item label=":">
					<el-date-picker v-model="search_data.startTime" type="datetime" placeholder="">
					</el-date-picker> &nbsp;&nbsp;&nbsp;
					<el-date-picker v-model="search_data.endTime" type="datetime" placeholder="">
					</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" icon="search" @click='onScreeoutMoney()'></el-button>
				</el-form-item>
				<el-form-item class="btnRight">
					<el-button type="primary" size="small" icon="view" @click='dialog.show=true'></el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="table_container">
			<el-table :data='departTable' max-height="650" border :default-sort="{prop: 'date', order: 'descending'}" style="width: 100%">
				<el-table-column prop="departName" label="" align='center' sortable width="300">
				</el-table-column>
				<el-table-column prop="departDesc" label="" align='center'>
				</el-table-column>
				<el-table-column label="" align="center" width="250">
					<template slot-scope="scope">
						<el-button size="mini" type="success" @click="detailDep(departTable[scope.$index])"></el-button>
						<el-button size="mini" @click="updataDepart(departTable[scope.$index])" type="info"></el-button>
						<el-button size="mini" type="danger" @click="deleteDepart(departTable[scope.$index])"></el-button>

					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-row>
				<el-col :span="24">
					<div class="pagination">
						<el-pagination v-if='paginations.total > 0' :page-sizes="paginations.page_sizes" :page-size="paginations.page_size"
						 :layout="paginations.layout" :total="paginations.total" :current-page.sync='paginations.page_index'
						 @current-change='handleCurrentChange' @size-change='handleSizeChange'>
						</el-pagination>
					</div>
				</el-col>
			</el-row>
		</div>

		<el-dialog :title="dialog.title" :visible.sync="dialog.show" :close-on-click-modal='false' :close-on-press-escape='false'
		 :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="">
					<el-input v-model="depDialog.departName" placeholder=" " clearable></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="depDialog.departDesc" type="textarea" :rows="4" placeholder=" " clearable></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="dialog.show=false"> </el-button>
					<el-button type="primary" @click="submitDep"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
		
		<el-dialog :title="updataDialog.title" :visible.sync="updataDialog.show" :close-on-click-modal='false'
		 :close-on-press-escape='false' :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="">
					<el-input v-model="upDetail.departName" placeholder=" " clearable></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="upDetail.departDesc" type="textarea" :rows="4" placeholder=" " clearable></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="updataDialog.show=false"> </el-button>
					<el-button type="primary" @click="submitUp"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
		<el-dialog :title="detailDialog.title" :visible.sync="detailDialog.show" :close-on-click-modal='false'
		 :close-on-press-escape='false' :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="">
					<el-input v-model="depDetail.departName" placeholder=" " :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="depDetail.departDesc" type="textarea" :rows="4" placeholder=" " :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="">
					<span class="departUser" v-for="(o, index) in depDetail.users" :key="index">
						{{o.userName}}
					</span>
				</el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="detailDialog.show=false"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
	</div>

</template>

<script>
	import qs from 'qs'
	export default {
		name: "department",
		inject: ['reload'],
		data() {
			return {
				departmentData: [{
					label: "",
					children: []
				}],
				departTable: [],
				allTableData: [],
				filterTableData: [],
				//
				paginations: {
					page_index: 1, // 
					total: 0, // 
					page_size: 5, // 1
					page_sizes: [5, 10, 15, 20], //
					layout: "total, sizes, prev, pager, next, jumper" // 
				},
				search_data: {
					startTime: "",
					endTime: ""
				},
				
				dialog: {
					show: false,
					title: "",
					option: "add"
				},
				depDialog: {},
				updataDialog: {
					show: false,
					title: "",
					option: "updata"
				},
				upDetail: {},
				detailDialog:{
					show: false,
					title: "",
					option: "detail"
				},
				depDetail: {},

			}
		},
		created() {
			this.getDepartment();
		},
		methods: {
						getDepartment() {
							this.$axios.get('/department/findDepartments').then(res => {
								// console.log(JSON.stringify(res.data.data)+"");
								this.departTable = res.data.data;
								this.allTableData= res.data.data;
								this.filterTableData= res.data.data;
							});
			
						},
			
			
			
			//
			detailDep(item) {   //idid
				this.detailDialog.show = true;
// 					this.$axios({
// 						method: 'get',
// 						url: '/department/findDepartByDepartId',
// 						params: {
// 							departId:id
// 						},
// 					}).then(res => {
// 						this.depDetail = res.data.data;
// 						// console.log(res.data.data)
// 					});
               this.depDetail = item;
			},
			
			
			//
			deleteDepart(dep) {
				if (!dep.users.length) {
					this.$confirm(', ?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning'
					}).then(() => { 

						this.$axios({
							method: 'get',
							url: '/department/deleteDepatById',
							params: {
								departId: dep.departId
							},
						}).then(res => {
							this.$message({
								type: 'success',
								message: '!'
							});
							this.reload();
						});

					}).catch(() => {
						this.$message({
							type: 'info',
							message: ''
						});
					});
				} else {
					this.$message({
						type: 'warning',
						message: '!'
					});
				}
			},
			
			
			//
			updataDepart(item) {
				this.upDetail = JSON.parse(JSON.stringify(item));
				this.updataDialog.show = true;
			},
			//
			submitUp(){
				var data = {
					departId: this.upDetail.departId,
					departName: this.upDetail.departName,
					departDesc: this.upDetail.departDesc
				}
				this.$axios({
					method: 'post',
					url: '/department/updateDapart',
					headers: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: qs.stringify(data)
				}).then(res => {
					this.$message({
						type: 'success',
						message: ''
					});
					this.updataDialog.show = false;
					this.reload();
				});
				
			},
			//
			submitDep() {
				this.$axios({
					method: 'post',
					url: '/department/saveDepart',
					data: qs.stringify(this.depDialog)
				}).then(res => {
					this.$message({
						type: 'success',
						message: ''
					});
					this.dialog.show = false;
					this.reload();
				});
			},
			handleCurrentChange(page) {
				// 
				let sortnum = this.paginations.page_size * (page - 1);
				let table = this.allTableData.filter((item, index) => {
					return index >= sortnum;
				});
				// 
				this.tableData = table.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			handleSizeChange(page_size) {
				// size
				this.paginations.page_index = 1;
				this.paginations.page_size = page_size;
				this.tableData = this.allTableData.filter((item, index) => {
					return index < page_size;
				});
			},
			setPaginations() {
				// 
				this.paginations.total = this.allTableData.length;
				this.paginations.page_index = 1;
				this.paginations.page_size = 5;
				// 
				this.tableData = this.allTableData.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},

		},

	}
</script>

<style>
	.infoshow {
		width: 100%;
		height: 100%;
		padding: 16px;
		box-sizing: border-box;
	}

	/* 
	.fillcontain {
		width: 100%;
		height: 100%;
		padding: 16px;
		box-sizing: border-box;
	} */

	.btnRight {
		float: right;
	}

	.pagination {
		text-align: right;
		margin-top: 10px;
	}
	.departUser{
		margin-right: 10px;
		font-weight: 600;
	}
</style>



