<template>
	<div class="whole">
		<template>
			<el-tabs v-model="activeName2" type="card" @tab-click="handleClick">
				<el-tab-pane label="" name="first">
					<template>
						<el-form :inline="true" ref="searchName" class="btnRight">
							<el-form-item label=":">
								<el-input type="name" v-model="searchName" clearable></el-input>
							</el-form-item>
							<el-form-item>
								<el-button type="primary" size="small" icon="search" @click='onScreeoutMoney()'></el-button>
							</el-form-item>

						</el-form>
						<el-table :data="tableData" style="width: 100%;border-top: 1px solid lightgray;" border :default-sort="{prop: 'date', order: 'descending'}">
							<el-table-column prop="createTime" label="" width="200" align='center' sortable>
							</el-table-column>
							<el-table-column prop="userName" label="" width="180" align='center'>
							</el-table-column>
							<el-table-column prop="realName" label="" align='center'>
							</el-table-column>
							<el-table-column prop="position" label="" align='center'>
							</el-table-column>
							<el-table-column prop='department.departName' label="" align='center'>
							</el-table-column>
							<el-table-column prop="phone" label="" align='center'>
							</el-table-column>
							<el-table-column prop="email" label="" align='center'>
							</el-table-column>
							<el-table-column prop="birthday" label="" align='center'>
							</el-table-column>
								<el-table-column label="" align="center">
									<template slot-scope="scope">
										<el-button size="mini" @click="updataCharact(tableData[scope.$index])" type="info"></el-button>
										<!-- <el-button size="mini" type="danger" @click="Delete(tableData[scope.$index].visitorId)"></el-button> -->
									</template>
								</el-table-column>
						</el-table>
						<!--  -->
						<el-row>
							<el-col :span="24">
								<div class="pagination">
									<el-pagination v-if='paginations.total > 0' :page-sizes="paginations.page_sizes" :page-size="paginations.page_size"
									 :layout="paginations.layout" :total="paginations.total" :current-page.sync='paginations.page_index'
									 @current-change='handleCurrentChange' @size-change='handleSizeChange'>

									</el-pagination>
								</div>
							</el-col>
						</el-row>
					</template>

				</el-tab-pane>
			<!-- 	<el-tab-pane label="" name="second"></el-tab-pane>
				<el-tab-pane label="" name="third"></el-tab-pane>
				<el-tab-pane label="" name="fourth"></el-tab-pane> -->
			</el-tabs>
		</template>
		
		
		<el-dialog :title="dialog.title" :visible.sync="dialog.show" :close-on-click-modal='false' :close-on-press-escape='false'
		 :modal-append-to-body="false">
			<el-form label-width="120px">
				<el-form-item label="">
					<el-input v-model="username"  :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="">
                    <el-select v-model="roleId" placeholder="">
                      <el-option v-for="(items,index) in roleList" :key="index" :label="items.roleDesc" :value="items.roleId" :id="items.roleId" ></el-option>   
                    </el-select>
                </el-form-item>
				<el-form-item>
					<el-button type="
					 info" @click="dialog.show=false"> </el-button>
					<el-button type="primary" @click="submitRole"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
	</div>
</template>
<script>
	import qs from 'qs'
	export default {
		data() {
			return {
				activeName2: 'first',
				tableData: [{
					date: '2016-05-02',
					name: '',
					address: ' 1518 '
				}, {
					date: '2016-05-04',
					name: '',
					address: ' 1517 '
				}, {
					date: '2016-05-01',
					name: '',
					address: ' 1519 '
				}, {
					date: '2016-05-03',
					name: '',
					address: ' 1516 '
				}],
				allTableData: [],
				filterTableData: [],
				//
				paginations: {
					page_index: 1, // 
					total: 0, // 
					page_size: 5, // 1
					page_sizes: [5, 10, 15, 20], //
					layout: "total, sizes, prev, pager, next, jumper" // 
				},
				searchName: "",
				dialog: {
					show: false,
					title: "",
					option: "edit"
				},
				roleId:'',
				username:'',
				roleList:[]
			};
		},
		created() {
			this.getProfile();
			
			this.getRoleList();
		},
		methods: {
			handleClick(tab, event) {
				console.log(tab, event);
			},
			getProfile() {
				// 
				this.$axios("/user/getUsers").then(res => {
					// console.log(res.data);
					this.tableData = res.data.data;
					this.allTableData = res.data.data;
					this.filterTableData = res.data.data;
					// console.log(JSON.stringify(this.tableData) + "||||");
					//
					this.setPaginations();
				});
			},
			onScreeoutMoney() { //user/getUserByRealname
			    const search_name=this.searchName;
				  this.$axios({
					  method:"get",
					  url:"/user/getUserByRealname",
					  params:{
						  realName:this.searchName
					  }
				  }).then(res => {
					this.tableData = res.data.data;
					this.allTableData = res.data.data;
					this.filterTableData = res.data.data;
					//console.log(JSON.stringify(this.tableData) + "||||");
					//
					this.setPaginations();
				});
			},
			updataCharact(users){
				//
				this.username=users.userName;
				this.dialog.show=true;
			},
			getRoleList(){
				//
				this.$axios.get('/user/getRoles').then(res =>{
					// console.log(res.data);
					this.roleList=res.data.data;
				});
			},
			submitRole(){ //
			const data1 ={
				userId:localStorage.getItem('userId'),
				roleId:this.roleId
			};
				this.$axios({
					method:'post',
					url:'/user/updateUserRole',
					data:qs.stringify(data1)
				}).then(res =>{
					// console.log(res.data);
					this.dialog.show=false;
					this.$message({
						message: "",
						type: "success"
					});
				});
			},
			handleCurrentChange(page) {
				// 
				let sortnum = this.paginations.page_size * (page - 1);
				let table = this.allTableData.filter((item, index) => {
					return index >= sortnum;
				});
				// 
				this.tableData = table.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			handleSizeChange(page_size) {
				// size
				this.paginations.page_index = 1;
				this.paginations.page_size = page_size;
				this.tableData = this.allTableData.filter((item, index) => {
					return index < page_size;
				});
			},
			setPaginations() {
				// 
				this.paginations.total = this.allTableData.length;
				this.paginations.page_index = 1;
				this.paginations.page_size = 5;
				// 
				this.tableData = this.allTableData.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},

		}
	}
</script>
<style scoped>
	.whole {
		padding: 18px;
	}

	.pagination {
		text-align: right;
		margin-top: 10px;
	}

	.btnRight {
		float: right;
	}
</style>



