let loading        //loading

function startLoading() {    //Element loading-start 
    loading = Loading.service({
        lock: true,
        text: '...',
        background: 'rgba(0, 0, 0, 0.7)'
    })
}
function endLoading() {    //Element loading-close 
    loading.close()
}



//
axios.interceptors.request.use(function (config) {
	  startLoading();
  // 
  // token,headertoken
  if (store.state.token) {
    config.headers.common['token'] = localStorage.getItem('token');
  }
  return config
}, function (error) {
  router.push('/login');
  return Promise.reject(error);
})
// 
axios.interceptors.response.use(function (response) {
  // 
	endLoading();
	if(response.data.msg=="no token"){
		Message.error({
			message:''
		});
		localStorage.clear();
		router.push('/login')
		
	}
  return response
}, function (error) {
	endLoading();
	// Message.error(error.response.data);
  // 
  if (error.response) {
    switch (error.response.status) {
      case 401:
        localStorage.clear();
        router.push('/login')
    }
  }
  return Promise.reject(error)
})

