<template>
	<div class="fillcontain">
		<div>
			<el-form :inline="true" ref="search_data" :model='search_data'>
				<el-form-item label=":">
					<el-date-picker v-model="search_data.startTime" type="datetime" placeholder="">
					</el-date-picker> &nbsp;&nbsp;&nbsp;
					<el-date-picker v-model="search_data.endTime" type="datetime" placeholder="">
					</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" icon="search" @click='onScreeoutMoney()'></el-button>
				</el-form-item>
				<el-form-item class="btnRight">
					<el-button type="primary" size="small" icon="view" @click='onAddMoney()'></el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="table_container">
			<el-table v-if="tableData" :data='tableData' max-height="650" border :default-sort="{prop: 'date', order: 'descending'}"
			 style="width: 100%">
				<el-table-column prop="interviewerName" label="" align='center'  sortable>
				</el-table-column>
				<el-table-column prop="phone" label="" align='center'>
				</el-table-column>
				<el-table-column prop="position" label="" align='center' >
				</el-table-column>
				<el-table-column prop="startTime" label="" align='center' >
				</el-table-column>
				<el-table-column prop="endTime" label="" align='center'>
				</el-table-column>
				<el-table-column prop="result" label="" align='center' >
				</el-table-column>
				<el-table-column prop="hrName" label="hr" align='center'>
				</el-table-column>
				<!-- <el-table-column label="" align="center">
					<template slot-scope="scope">
						<el-button size="mini" @click="updataFound(tableData[scope.$index])" type="info"></el-button>
						<el-button size="mini" type="danger" @click="Delete(tableData[scope.$index].visitorId)"></el-button>
					</template>
				</el-table-column> -->
			</el-table>
			<!--  -->
			<el-row>
				<el-col :span="24">
					<div class="pagination">
						<el-pagination v-if='paginations.total > 0' :page-sizes="paginations.page_sizes" :page-size="paginations.page_size"
						 :layout="paginations.layout" :total="paginations.total" :current-page.sync='paginations.page_index'
						 @current-change='handleCurrentChange' @size-change='handleSizeChange'>
						</el-pagination>
					</div>
				</el-col>
			</el-row>
		</div>
		<!--  -->
		<DialogFound :dialog='dialog' :form='form' @update="getProfile" ></DialogFound>
		<FoundDialog :fdialog='fdialog' :fform='fform' @update="getProfile"></FoundDialog>
	</div>
</template>

<script>
	import DialogFound from "../components/DialogFound";
	import FoundDialog from "../components/foundDialog";

	export default {
		name: "fundlist",
		data() {
			return {
				// apiurl: 'http://192.168.110.189/',
				tableData: [],
				allTableData: [],
				filterTableData: [],
				dialog: {
					show: false,
					title: "",
					option: "edit"
				},
				form: {
					 interviewId: "",
					 interviewerName: "",
					 position: "",
					 phone: "",
					 startTime: "",
					 endTime: "",
					 result: "",
					 hrName: ""
				},
				fdialog: {
					show: false,
					title: "",
					option: "updata"
				},
				fform: {
					 interviewId: "",
					 interviewerName: "",
					 position: "",
					 phone: "",
					 startTime: "",
					 endTime: "",
					 result: "",
					 hrName: ""
				},
				//
				paginations: {
					page_index: 1, // 
					total: 0, // 
					page_size: 5, // 1
					page_sizes: [5, 10, 15, 20], //
					layout: "total, sizes, prev, pager, next, jumper" // 
				},
				search_data: {
					startTime: "",
					endTime: ""
				}
			};
		},
		components: {
			DialogFound,
			FoundDialog
		},
		created() {
			this.getProfile();
		},
		methods: {
			getProfile() {
				// 
				this.$axios("/interview/getAllInterview").then(res => {
					this.tableData = res.data.data;
					this.allTableData = res.data.data;
					this.filterTableData = res.data.data;
					// console.log(res.data.data)
					// 
					this.setPaginations();
				});
			},
			onAddMoney() {
				// 
				this.dialog = {
					show: true,
					title: "",
					option: "add"
				};
				this.form = {
			    }
			},
			updataFound(found){
				console.log(JSON.stringify(found));
				this.fdialog = {
					show: true,
					title: "",
					option: "updata"
				};
				this.fform = JSON.parse(JSON.stringify(found));
			},
			handleCurrentChange(page) {
				// 
				let sortnum = this.paginations.page_size * (page - 1);
				let table = this.allTableData.filter((item, index) => {
					return index >= sortnum;
				});
				// 
				this.tableData = table.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			handleSizeChange(page_size) {
				// size
				this.paginations.page_index = 1;
				this.paginations.page_size = page_size;
				this.tableData = this.allTableData.filter((item, index) => {
					return index < page_size;
				});
			},
			setPaginations() {
				// 
				this.paginations.total = this.allTableData.length;
				this.paginations.page_index = 1;
				this.paginations.page_size = 5;
				// 
				this.tableData = this.allTableData.filter((item, index) => {
					return index < this.paginations.page_size;
				});
			},
			onScreeoutMoney() {
				// 
				if (!this.search_data.startTime || !this.search_data.endTime) {
					this.$message({
						type: "warning",
						message: ""
					});
					this.getProfile();
					return;
				}
				const stime = this.search_data.startTime.getTime();
				const etime = this.search_data.endTime.getTime();
				this.allTableData = this.filterTableData.filter(item => {
					let date = new Date(item.date);
					let time = date.getTime();
					return time >= stime && time <= etime;
				});
				// 
				this.setPaginations();
			}
		}
	};
</script>
<style scoped>
	.fillcontain {
		width: 100%;
		height: 100%;
		padding: 16px;
		box-sizing: border-box;
	}

	.btnRight {
		float: right;
	}

	.pagination {
		text-align: right;
		margin-top: 10px;
	}
</style>



